*{margin:0;padding:0;font-family: sans-serif;box-sizing: border-box;}
a{text-decoration: none;}
.main{
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 15% 28.333% 28.333% 28.333% /*header ,  about-me , portfolio , contact*/
}

.header{
   display:grid;
}

.rotated-content{
    display: flex;
    justify-content: center;
    align-items: center;
}


.socials-container{
    width: 90%;
    justify-self:center;
    
}

.socials{
    display: flex;
    justify-content: space-between;
    list-style: none;
}


#about-me{background: #222222;height: 100vh;display: flex; flex-direction: column;justify-content: center; align-items:center;color:#f3f3f3}
#portfolio{background: #333; height: 100vh;display: flex; justify-content: center;align-items: center;}
#contact{background: #444; height: 100vh;display: flex; justify-content: center;align-items: center;}

.section-title{
    color:#f3f3f3;
    align-self: center;
    margin-bottom: 15%;
}

.github{
    content: url(github-logo.svg);
}

.twitter{
    content: url(twitter-logo.svg);
}

.linkedin{
    content: url(linkedin-logo.svg);
}

.icon{  width: 100%;}


.about-me-inner{width: 60%;
    overflow-y:auto;
    overflow-x: hidden;
    padding: 15px;
}

.about-me-inner>h1{
    font-size: 3rem;
}

.about-me-inner>p{
    font-size: 1.5rem;
}

.about-me-inner::-webkit-scrollbar-track
{

	background-color: #f3f3f3;
}

.about-me-inner::-webkit-scrollbar
{
	width: 10px;
	background-color: #f3f3f3;
}

 .about-me-inner::-webkit-scrollbar-thumb
{
	background-color: #999;	
}

.portfolio-inner{
    width: 90%;
    height: 90vh;
    overflow-x: hidden;
    overflow-y:auto;
}

.portfolio-inner>li{
    display: inline-block;
    vertical-align: top;
    margin: 2%;
    width: 300px;
    height: 300px;
    background-color: #c4c4c4;
}

.portfolio-inner::-webkit-scrollbar-track
{

	background-color: #f3f3f3;
}

.portfolio-inner::-webkit-scrollbar
{
	width: 10px;
	background-color: #f3f3f3;
}

.portfolio-inner::-webkit-scrollbar-thumb
{
	background-color: #999;	
}

.contact-inner{
display: flex;
width: 100%;
justify-content: center;
}

.contact-inner>form>ul{
list-style: none;
}

.contact-inner>form>ul>li{
    margin-bottom: 2%;
}

.first-line{
    display: inline-block;
}

.mail{
    width: 98%;
    margin-left: 2%;
}
.name{
    width: 98%;

}
.subject{
    width: 100%;
}

input{
    height: 2em;
    padding: 15px;
}

button{
    padding: .6em;
}

textarea{
    width: 100%;
    height:150px;
    padding: 15px;
}

.description{width: 100%;height: 100%;background-color: rgba(51, 51, 51,0.7);padding: 15px;}

.description span:first-child{
    color:#f3f3f3;
    display: block;
    font-weight: bold;
    font-size: 1.5rem;
    width: 180px;
    margin-bottom: 15px;
}

.description span:last-child{
    margin-top: 15px;
    color:#f3f3f3;
    width: 180px;
    font-style: oblique;
}